import React, { Dispatch, SetStateAction, useState } from "react"
import { useSearchParams } from "react-router-dom"
import { FILES_FEATURE_NAME } from "@rp/common/settings"
import { useFilesOwnersList, useQuota } from "@rp/react/tanstack-api/file"
import { QuotaReachedMessage } from "@rp/react/web/common/composite-components"
import { useModal } from "@rp/react/web/common/providers"
import { FilesUploadDropZone } from "@rp/react/web/files"
import { Box } from "@rp/react/web/mui/core"
import { SelectedFile } from "../../../../../components/Hub/Files/Files"
import { FilesListV2 } from "../../../../../components/Hub/Files/FilesList/FilesListV2"
import { SelectItem } from "../../filesPage.types"
import { FilesPaneFilters } from "./FilesPaneFilters"
import { FilesPaneHeader } from "./FilesPaneHeader"

type FilesPaneProps = {
  width: string | null
  openChatPane: () => void
  isChatPaneOpen: boolean
  selectedFiles: Array<SelectedFile>
  setSelectedFiles: Dispatch<SetStateAction<Array<SelectedFile>>>
}

function FilesPane({
  width,
  openChatPane,
  isChatPaneOpen,
  selectedFiles,
  setSelectedFiles,
}: FilesPaneProps) {
  const {
    planComparisonModal: { handleOpen: planComparisonHandleOpen },
  } = useModal()

  const { disableUpload } = useQuota()
  const [searchParams] = useSearchParams()
  const [ownersSelected, setOwnersSelected] = useState<Array<SelectItem>>([])
  const { data: ownersList, isPending: isOwnersListPending } = useFilesOwnersList()

  if (!width) return null

  const filterTerm = searchParams.get("find") ?? ""

  const overlayMarginOffset = "118px"

  return (
    <Box sx={{ width, transition: "0.2s ease-in-out", minWidth: "350px" }}>
      <FilesUploadDropZone
        disabled={disableUpload}
        customDropZoneAreaStyle={{
          display: "block",
          overflow: "inherit",
        }}
        customFileUploadOverlayStyle={{
          height: `calc(100% - ${overlayMarginOffset})`,
          marginTop: overlayMarginOffset,
        }}
      >
        <Box sx={{ mx: 2 }}>
          <FilesPaneHeader
            selectedFiles={selectedFiles}
            filterTerm={filterTerm}
            disableUpload={disableUpload}
            planComparisonHandleOpen={planComparisonHandleOpen}
          />

          <FilesPaneFilters
            setOwnersSelected={setOwnersSelected}
            ownersSelected={ownersSelected}
            ownersList={ownersList}
            isOwnersListPending={isOwnersListPending}
          />

          <Box>
            <Box sx={{ position: "relative", pt: 2 }}>
              <QuotaReachedMessage onClick={() => planComparisonHandleOpen(FILES_FEATURE_NAME)} />
            </Box>

            <FilesListV2
              selectedFiles={selectedFiles}
              filterTerm={filterTerm}
              openChatPane={openChatPane}
              ownersSelected={ownersSelected}
              ownersList={ownersList}
              isChatPaneOpen={isChatPaneOpen}
              setSelectedFiles={setSelectedFiles}
            />
          </Box>
        </Box>
      </FilesUploadDropZone>
    </Box>
  )
}

export { FilesPane }
